import React, { Component } from 'react';
import './CategoryFormatter.css';

class CategoryFormatter extends Component {

    createCatButton = (categories) => {
        if (categories === null) {
            return <button className="cat-button-hide">Nothing</button>;
        } else {
            var categoryarr = categories.split(",");
            var buttons = categoryarr.map((category, index) => {
                return (
                    <button
                        key={index}
                        className="cat-button">{category}</button>
                )
            })
            return buttons;
        }

    }
    render() {
        return (
            <React.Fragment>
                {this.createCatButton(this.props.categories)}
            </React.Fragment>
        )

    }
}
export default CategoryFormatter