import React, { Component } from "react";
import CampaignChoose from '../../campaignpage/campaignchoose/CampaignChoose';
import CategoryFormatter from '../../utilities/categoryformatter/CategoryFormatter'
import './ListCampaignContent.css';

class ListCampaignContent extends Component {
    componentDidMount() {
        document.getElementById("List1").style.display = "block";
    }
    getBigger(x, y) {
        x = parseInt(x);
        y = parseInt(y);
        var z = Math.max(x, y);
        return z;
    }
    checkLength(name, x) {
        if (name.length >= x) {
            return name.substring(0, x) + "..";
        } else {
            return name;
        }
    }
    renderList = (data) => {
        if (data !== "") {
            let listcard = data.map((card, index) => {
                return (
                    <div key={index} className="item row">
                        <div className="col-sm-2 img-cont-shortlist text-center">
                            <img src={card.photo} alt="" />
                        </div>
                        <div className="col-sm-2 mid">
                            <h5>@{this.checkLength(card.username, 12)}</h5>
                            <h6>{this.checkLength(card.full_name, 12)}</h6>
                            <h6><i className="fa fa-map-marker"></i>&nbsp;{card.location_city}</h6>
                        </div>
                        <div className="col-sm-2 mid">
                            <h6 className="cat-button-holder"><CategoryFormatter categories={card.categories} /></h6>
                            {card.Comments !== "" ? <h6><i className="fa fa-comment"></i>&nbsp;{card.Comments}</h6> : null}
                        </div>
                        <div className="col-sm-6 row mid">
                            <div className="col text-center">
                                <h5 className="font-weight-bolder">{card.authenticity_score}</h5>
                                <h6>Authenticity Score</h6>
                            </div>
                            <div className="col text-center">
                                <h5 className="font-weight-bolder">{card.followers}</h5>
                                <h6>Followers</h6>
                            </div>
                            <div className="col text-center">
                                <h5 className="font-weight-bolder">{this.getBigger(card.avg_picture_engagement, card.avg_video_engagement)}%</h5>
                                <h6>Engagement</h6>
                            </div>
                        </div>
                    </div>
                )
            })
            return listcard;
        }

    }
    render() {
        const { type, id, data } = this.props;
        let list_id = type + id;
        let list_name = null;
        if (id === "new") {
            list_name = "Create New " + type;
        } else {
            list_name = type + " " + id;
        }

        return (
            <div id={list_id} className="tabcontent">
                <button className="topbutton m-0">{list_name}</button>
                {this.renderList(data)}
                <div className="buttons">
                    <button className="contentbutton">Save List</button>
                    <button className="contentbutton" data-toggle="modal" data-target="#exampleModalCenter"> Assign Campaign</button>
                </div>
                <CampaignChoose />
            </div>
        )
    }
}
export default ListCampaignContent;