const initialState = {
  items: [],
  itemsCountPerPage: 16,
  influencersCount: 0
};

const influencers = (state = initialState, action) => {
  switch (action.type) {
    case "UPDATE_INFLUENCER_DATA":
      return {
        items: action.influencerData.data,
        itemsCountPerPage: 16,
        influencersCount: action.influencerData.influencer_count
      };
    default:
      return state;
  }
};

export default influencers;
