import React, { Component } from 'react';
import './CheckBox.css';
import Select from '../../filterform/select/Select'

class CheckBox extends Component {

  render() {
    return (
      <div className={this.props.outerdiv} >
        <Select
          class={this.props.innerdiv}
          belongsTo={this.props.partOf}
          placeholder="Assign To Lists"
          options={[
            { value: 'List 1' },
            { value: 'List 2' },
            { value: 'Create New' },
          ]
          }
          multiple
        />
      </div>
    );
  }
}
export default CheckBox;