import React, { Component } from 'react'
import ChevronDown from '../../../utilities/ChevronDown';
import ChevronUp from '../../../utilities/ChevronUp';
import ChevronLeft from '../../../utilities/ChevronLeft';
import ChevronRight from '../../../utilities/ChevronRight';
import './Select.css'


class Select extends Component {
  constructor(props) {
    super(props);
    this.state = {
      values: [],
      focusedValue: -1,
      isFocused: false,
      isOpen: false,
      typed: ''
    }

    this.onFocus = this.onFocus.bind(this);
    this.onBlur = this.onBlur.bind(this);
    this.onKeyDown = this.onKeyDown.bind(this);

    this.onClick = this.onClick.bind(this);
    this.onDeleteOption = this.onDeleteOption.bind(this);
    this.onHoverOption = this.onHoverOption.bind(this);
    this.onClickOption = this.onClickOption.bind(this);

    this.renderOption = this.renderOption.bind(this);
  }

  onFocus() {
    this.setState({
      isFocused: true
    })
  }

  onBlur() {
    const { options, multiple } = this.props;


    this.setState(prevState => {
      const { values } = prevState

      if (multiple) {
        return {
          focusedValue: -1,
          isFocused: false,
          isOpen: false
        }
      } else {
        const value = values[0]

        let focusedValue = -1

        if (value) {
          focusedValue = options.findIndex(option => option.value === value)
        }

        return {
          focusedValue,
          isFocused: false,
          isOpen: false
        }
      }
    })
  }

  onKeyDown(e) {
    const { options, multiple } = this.props
    const { isOpen } = this.state

    switch (e.key) {
      case ' ':
        e.preventDefault()
        if (isOpen) {
          if (multiple) {
            this.setState(prevState => {
              const { focusedValue } = prevState

              if (focusedValue !== -1) {
                const [...values] = prevState.values
                const value = options[focusedValue].value
                const index = values.indexOf(value)

                if (index === -1) {
                  values.push(value)
                } else {
                  values.splice(index, 1)
                }

                return { values }
              }
            })
          }
        } else {
          this.setState({
            isOpen: true
          })
        }
        break
      case 'Escape':
      case 'Tab':
        if (isOpen) {
          e.preventDefault()
          this.setState({
            isOpen: false
          })
        }
        break
      case 'Enter':
        this.setState(prevState => ({
          isOpen: !prevState.isOpen
        }))
        break
      case 'ArrowDown':
        e.preventDefault()
        this.setState(prevState => {
          let { focusedValue } = prevState

          if (focusedValue < options.length - 1) {
            focusedValue++

            if (multiple) {
              return {
                focusedValue
              }
            } else {
              return {
                values: [options[focusedValue].value],
                focusedValue
              }
            }
          }
        })
        break
      case 'ArrowUp':
        e.preventDefault()
        this.setState(prevState => {
          let { focusedValue } = prevState

          if (focusedValue > 0) {
            focusedValue--

            if (multiple) {
              return {
                focusedValue
              }
            } else {
              return {
                values: [options[focusedValue].value],
                focusedValue
              }
            }
          }
        })
        break
      default:
        if (/^[a-z0-9]$/i.test(e.key)) {
          const char = e.key

          clearTimeout(this.timeout)
          this.timeout = setTimeout(() => {
            this.setState({
              typed: ''
            })
          }, 1000)

          this.setState(prevState => {
            const typed = prevState.typed + char
            const re = new RegExp(`^${typed}`, 'i')
            const index = options.findIndex(option => re.test(option.value))

            if (index === -1) {
              return {
                typed
              }
            }

            if (multiple) {
              return {
                focusedValue: index,
                typed
              }
            } else {
              return {
                values: [options[index].value],
                focusedValue: index,
                typed
              }
            }
          })
        }
        break
    }
  }

  onClick() {
    this.setState(prevState => ({
      isOpen: !prevState.isOpen
    }))
  }

  onDeleteOption(e) {
    const { value } = e.currentTarget.dataset

    this.setState(prevState => {
      const [...values] = prevState.values
      const index = values.indexOf(value)

      values.splice(index, 1)

      return { values }
    })
  }

  onHoverOption(e) {
    const { options } = this.props

    const { value } = e.currentTarget.dataset
    const index = options.findIndex(option => option.value === value)

    this.setState({
      focusedValue: index
    })
  }

  onClickOption(e) {
    const { multiple } = this.props

    const { value } = e.currentTarget.dataset

    this.setState(prevState => {
      if (!multiple) {
        return {
          values: [value],
          isOpen: false
        }
      }

      const [...values] = prevState.values
      const index = values.indexOf(value)

      if (index === -1) {
        values.push(value)
      } else {
        values.splice(index, 1)
      }

      return { values }
    });
  }

  stopPropagation(e) {
    e.stopPropagation()
  }

  renderValues(type) {
    const { placeholder, multiple } = this.props
    const { values } = this.state
    if (type === "card") {
      return (
        <div className="placeholder">
          {placeholder}
        </div>
      )
    } else {
      if (values.length === 0) {
        return (
          <div className="placeholder">
            {placeholder}
          </div>
        )
      }

      if (multiple) {
        if (values.length === 1) {
          return (
            <span
              key={values[0]}
              onClick={this.stopPropagation}
              className="multiple value"
            >
              {values[0]}
              <span
                data-value={values[0]}
                onClick={this.onDeleteOption}
                className="delete"
              >
                <X />
              </span>
            </span>

          )
        }
        if (values.length > 1) {
          if (values[0].length < 10) {
            return (
              <div>
                <span
                  key={values[0]}
                  onClick={this.stopPropagation}
                  className="multiple value"
                >
                  {values[0]}
                  <span
                    data-value={values[0]}
                    onClick={this.onDeleteOption}
                    className="delete"
                  >
                    <X />
                  </span>
                </span>
                <p className="more-text">+{(values.length) - 1} more</p>
              </div>
            )
          } else {
            return (
              <div>
                <span
                  key={values[0]}
                  onClick={this.stopPropagation}
                  className="multiple value"
                >
                  {values[0]}
                  <span
                    data-value={values[0]}
                    onClick={this.onDeleteOption}
                    className="delete"
                  >
                    <X />
                  </span>
                </span>
                <p className="more-text">+{(values.length) - 1} </p>
              </div>
            )
          }

        }
      }

      return (
        <div className="value">
          {values[0]}
        </div>
      )

    }

  }

  renderOptions(type) {
    const { options } = this.props
    const { isOpen } = this.state;

    if (!isOpen) {
      return null
    }
    let className = "";
    if (type === "form") {
      className = "options";
    } else
      if (type === "small") {
        className = "options-card-small";
      } else {
        className = "options-card-big";
      }
    return (
      <div className={className}>
        {options.map(this.renderOption)}
      </div>
    )
  }

  renderOption = (option, index) => {
    const { multiple } = this.props
    const { values, focusedValue } = this.state

    const { value } = option

    const selected = values.includes(value)

    let className = "option";

    if (selected) className += " selected"
    if (index === focusedValue) className += " focused"

    return (
      <div
        key={value}
        data-value={value}
        className={className}
        onMouseOver={this.onHoverOption}
        onClick={this.onClickOption}
      >
        {multiple ?
          <span className="checkbox">
            {selected ? <Check /> : null}
          </span> :
          null
        }
        {value}
      </div>
    )
  }
  renderAccordingly(type) {
    const { isOpen } = this.state
    if (type === "form") {
      return (
        <React.Fragment>
          <div className="selection" onClick={this.onClick}>
            {this.renderValues("form")}
            <span className="arrow">
              {isOpen ? <ChevronUp /> : <ChevronDown />}
            </span>
          </div>
          {this.renderOptions("form")}
        </React.Fragment>
      )
    } else {
      return (
        <React.Fragment>
          <div className="selection" onClick={this.onClick}>
            {this.renderValues("card")}
            <span className="arrow-card">
              {isOpen ? <ChevronLeft /> : <ChevronRight />}
            </span>
          </div>
          {this.renderOptions(type)}
        </React.Fragment>

      )
    }
  }
  render() {
    return (
      <React.Fragment>
        <div
          className={this.props.class}
          tabIndex="0"
          onFocus={this.onFocus}
          onBlur={this.onBlur}
          onKeyDown={this.onKeyDown}
        >
          {this.renderAccordingly(this.props.belongsTo)}
        </div>
        <input type="hidden" id={this.props.id} value={this.state.values} />
      </React.Fragment>
    )
  }
}




const X = () => (
  <svg viewBox="0 0 16 16">
    <path d="M2 .594l-1.406 1.406.688.719 5.281 5.281-5.281 5.281-.688.719 1.406 1.406.719-.688 5.281-5.281 5.281 5.281.719.688 1.406-1.406-.688-.719-5.281-5.281 5.281-5.281.688-.719-1.406-1.406-.719.688-5.281 5.281-5.281-5.281-.719-.688z" />
  </svg>
)

const Check = () => (
  <svg viewBox="0 0 16 16">
    <path d="M13 .156l-1.406 1.438-5.594 5.594-1.594-1.594-1.406-1.438-2.844 2.844 1.438 1.406 3 3 1.406 1.438 1.406-1.438 7-7 1.438-1.406-2.844-2.844z" transform="translate(0 1)" />
  </svg>
)



export default Select