import React, { Component } from "react";
import { connect } from "react-redux";

import "./FilterForm.css";

import Select from "./select/Select";
import Follower from "./follower/Follower";
import { getFilter } from '../../../getFilterFormValues';

import { fetchInfluencerData, fetchFilterMenuData } from "../../../actions";

class FilterForm extends Component {

  componentDidMount() {
    this.props.fetchFilterMenuData("category_names");
  }
  getFormattedMenusArray(menujson) {
    if (menujson !== null) {
      let menuDataArray = [];
      menujson.map(category => {
        let categoryobj = {
          "value": category.fields.name
        }
        menuDataArray.push(categoryobj);
      })
      return menuDataArray;
    } else {
      return null;
    }

  }
  handleSubmit(e) {
    e.preventDefault();
    const filter = getFilter();
    const url = `https://secure-bastion-20455.herokuapp.com/influencer_listings/search?${filter.categories}${filter.locations}minfollowers=${filter.minfollowers}&maxfollowers=${filter.maxfollowers}${filter.engagement}`;
    this.props.fetchInfluencerData(url);
  }
  render() {

    return (
      <form
        className="form-cont"
        id="filter-form"
        onSubmit={this.handleSubmit.bind(this)}
      >
        <div className="select-cont">
          <Select
            class="select"
            id="category"
            belongsTo="form"
            placeholder="Categories"
            options={this.getFormattedMenusArray(this.props.menuData)}
            multiple
          />
          <span style={{ display: "inline-block", width: 10 }} />
          <Select
            class="select"
            belongsTo="form"
            id="location"
            placeholder="Locations"
            options={[
              { value: "Delhi NCR" },
              { value: "Mumbai" },
              { value: "Banglore" },
              { value: "Hyderabad" },
              { value: "Chennai" },
              { value: "Kolkata" },
              { value: "Pune" },
              { value: "Ahmedabad" },
              { value: "Surat" },
              { value: "Goa" },
              { value: "Jaipur" },
              { value: "North India Tier 2&3" },
              { value: "South India Tier 2&3" },
              { value: "Central India Tier 2&3" },
              { value: "North-East Indian Cities" }
            ]}
            multiple
          />
          <span style={{ display: "inline-block", width: 10 }} />
          <Select
            class="select"
            belongsTo="form"
            placeholder="Engagement"
            id="engagement"
            options={[
              { value: "1-5%" },
              { value: "5-10%" },
              { value: "10-20%" },
              { value: "20-30%" },
              { value: "30-40%" },
              { value: "40-50%" },
              { value: ">50%" }
            ]}
            multiple
          />
          <span style={{ display: "inline-block", width: 10 }} />
          <Follower />
        </div>
        <div className="search-cont">
          <div>
            <button className="search-button">
              <i className="fa fa-search" />
              &nbsp;&nbsp;Search
            </button>
          </div>
        </div>
      </form>
    );
  }
}
const mapStateToProps = state => ({
  menuData: state.filtersMenu
});
const mapDispatchToProps = dispatch => {
  return {
    fetchInfluencerData: url => {
      dispatch(fetchInfluencerData(url));
    },
    fetchFilterMenuData: MenuType => {
      dispatch(fetchFilterMenuData(MenuType));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FilterForm);
