import React, { Component } from "react";
import { connect } from "react-redux";

import Loader from "../loader/Loader";
import Header from '../header/Header'
import ListCampaignButton from "../utilities/listcamapignbutton/ListCampaignButton";
import ListCampaignContent from "../utilities/listcampaigncontent/ListCampaignContent";

import {
  setIsDataLoading,
  receiveInfluencerData,
  fetchInfluencerData
} from "../../actions";
import "./ShortListPage.css";

class ShortListPage extends Component {
  myOptions = {
    method: "GET",
    cache: "default",
    headers: {}
  };
  componentDidMount() {
    // this.props.fetchLists();
  }

  renderList() {
    if (!this.props.isloading) {
      return (
        <div className="row" id="container">
          <div className="tab col-sm-3" id="listcont">
            <button className="topbutton mt-0">Select List</button>
            {this.props.lists.map((key, index) => {
              return (
                <ListCampaignButton key={key.No} data={key.No} type="List" />
              );
            })}
            <ListCampaignButton
              data="new"
              type="List"
            // onClick={}
            />
          </div>
          <div className="col-sm-9 main-content" id="list-content">
            {this.props.lists.map(key => {
              return (
                <ListCampaignContent
                  key={key.No}
                  id={key.No}
                  data={key.list}
                  type="List"
                />
              );
            })}
            <ListCampaignContent id="new" data="" type="List" />
          </div>
        </div>
      );
    } else {
      return <Loader />;
    }
  }
  render() {
    return (
      <React.Fragment>
        <Header />
        <hr />
        {this.renderList()}
      </React.Fragment>

    );
  }
}

const mapStateToProps = state => ({
  lists: state.lists.lists,
  isloading: state.lists.isloading
});
export default connect(mapStateToProps)(ShortListPage);
