import React, { Component } from "react";
import './ListCampaignButton.css';

class ListCampaignButton extends Component {
    openList(type, id) {
        let i, tablinks, tabcontent;
        tablinks = document.getElementsByClassName("tablinks");
        for (i = 0; i < tablinks.length; i++) {
            tablinks[i].className = tablinks[i].className.replace(" active", "");
        }
        let tabName = type + id;
        let button = "button-" + type + id;
        tabcontent = document.getElementsByClassName("tabcontent");
        for (i = 0; i < tabcontent.length; i++) {
            tabcontent[i].style.display = "none";
        }
        document.getElementById(tabName).style.display = "block";
        document.getElementById(button).className += " active";
    }

    render() {
        const { data, type } = this.props;
        var b_id = "button-" + type + data;
        let button_name = null;
        if (data === "new") {
            button_name = "Create New " + type;
        } else {
            button_name = type + " " + data;
        }
        let classname = ["tablinks", "tabbutton"];
        if (this.props.data === 1) {
            classname.push("active");
        }
        return (
            <button
                className={classname.join(' ')}
                onClick={() => { this.openList(this.props.type, this.props.data) }}
                id={b_id}
            >{button_name}</button>
        )
    }

}
export default ListCampaignButton;