import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import '../loginSignupForm.css';

const emailRegex = RegExp(
  /^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
);

const formValid = ({ formErrors, ...rest }) => {
  let valid = true;

  Object.values(formErrors).forEach(val => {
    val.length > 0 && (valid = false);
  });

  Object.values(rest).forEach(val => {
    val === null && (valid = false);
  });

  return valid;
};

class LoginForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: null,
      password: null,
      formErrors: {
        email: "",
        password: ""
      }
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    if (formValid(this.state)) {
      fetch('https://secure-bastion-20455.herokuapp.com/accounts/login', {
        method: 'POST',
        body: this.state,
        headers: {
          // 'Access-Control-Allow-Origin':'*',
          'Content-Type': 'application/json',
        }
      });
    } else {
      console.error("FORM INVALID -DISPLAY ERROR MESSAGE")
    }

  };

  handleChange = e => {
    e.preventDefault();
    const { name, value } = e.target;
    let formErrors = { ...this.state.formErrors };

    switch (name) {
      case "email":
        formErrors.email = emailRegex.test(value) ? "" : "invalid email address";
        break;
      case "password":
        formErrors.password =
          value.length < 8 ? "minimum 8 characters" : "";
        break;
      default:
        break;
    }

    this.setState({ formErrors, [name]: value });
  };

  render() {
    const { formErrors } = this.state;
    return (
      <div className="wrapper">
        <div className="form-wrapper">
          <h1>Log In</h1>
          <form onSubmit={this.handleSubmit} noValidate className="login-signup-form">
            <div className="input-width">
              <input
                className={formErrors.email.length > 0 ? "error" : null}
                placeholder="Email"
                type="email"
                name="email"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.email.length > 0 && (
                <span className="errorMessage">{formErrors.email}</span>
              )}
            </div>
            <div className="input-width">
              <input
                className={formErrors.password.length > 0 ? "error" : null}
                placeholder="Password"
                type="password"
                name="password"
                id="password"
                noValidate
                onChange={this.handleChange}
              />
              {formErrors.password.length > 0 && (
                <span className="errorMessage">{formErrors.password}</span>
              )}
            </div>
            <div className="form-lower-section">
              <button type="submit">Log in</button>
              <Link to="/signup"><small>New Here ? Create Account</small></Link>
            </div>
          </form>
        </div>
      </div>
    )
  }
}


export default LoginForm;