import React, { Component } from "react";
import "./Follower.css";
import ChevronUp from "../../../utilities/ChevronUp";
import ChevronDown from "../../../utilities/ChevronDown";

class Follower extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showItems: false,
      minval: 0,
      maxval: 0
    };
    this.onchange = this.onchange.bind(this);
  }

  onchange = async e => {
    await this.setState({ [e.target.name]: e.target.value });
  };
  dropDown = () => {
    this.setState(prevState => ({
      showItems: !prevState.showItems
    }));
  };
  render() {
    return (
      <div style={{ display: "inline-block" }}>
        <div className="box ">
          <div className="box-container">
            <div className="heading" onClick={this.dropDown}>
              Followers
            </div>
            <div className="fol-arrow" onClick={this.dropDown}>
              <span>
                {this.state.showItems ? <ChevronUp /> : <ChevronDown />}
              </span>
            </div>
          </div>
          <div
            className="fol-filter-box"
            style={{ display: this.state.showItems ? "block" : "none" }}
          >
            <div>
              <input
                type="number"
                name="minval"
                id="minval"
                placeholder="min"
                className="fol-input"
                onChange={this.onchange}
              />
              to
              <input
                type="number"
                name="maxval"
                id="maxval"
                placeholder="max"
                className="fol-input"
                onChange={this.onchange}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Follower;
