import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import Pagination from "react-js-pagination";
import { getFilter } from '../../../getFilterFormValues';

import "./CardContainer.css";

import Loader from "../../loader/Loader";
import InfluencerCard from "./influencercard/InfluencerCard";
import OrderByCounterContainer from '../OrderByCounterContainer/OrderByCounterContainer';
import { fetchInfluencerData } from "../../../actions";

class CardContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activePage: 1,
    };
  }

  componentDidMount() {
    const url = "https://secure-bastion-20455.herokuapp.com/?page=1";
    this.props.fetchInfluencerData(url);
  }

  handlePageChange(pageNumber) {
    this.setState({ activePage: pageNumber });
    const filter = getFilter();
    const url = `https://secure-bastion-20455.herokuapp.com/influencer_listings/search?page=${pageNumber}&${filter.categories}${filter.locations}minfollowers=${filter.minfollowers}&maxfollowers=${filter.maxfollowers}&${filter.engagement}`;
    this.props.fetchInfluencerData(url);
  }


  renderCards() {
    if (!this.props.isDataLoading) {
      if (this.props.items.length === 0) {
        return (
          <div className="row slides no-data-found ">
            <img src="https://camo.githubusercontent.com/472c00f642bd004e55ba0771541138593eb23a53/687474703a2f2f6564756d6f74652e636f6d2f6173736574732f696d616765732f736c696465722f6e6f7464617461666f756e642e706e67" alt="" />
          </div>
        )
      } else {
        return (
          <div className="row slides">
            {this.props.items.map((key, index) => {
              return <InfluencerCard key={index} id={key.id} data={key} />;
            })}
          </div>
        );
      }
    } else {
      return <Loader />;
    }
  }
  render() {
    return (
      <React.Fragment>
        <OrderByCounterContainer influencersCount={this.props.influencersCount} />
        {this.renderCards()}
        <div className="d-flex justify-content-center">
          <Pagination
            activePage={this.state.activePage}
            itemsCountPerPage={this.props.itemsCountPerPage}
            totalItemsCount={this.props.influencersCount}
            pageRangeDisplayed={5}
            onChange={this.handlePageChange.bind(this)}
            itemClass="page-item"
            linkClass="page-link"
          />
        </div>
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => ({
  items: state.influencers.items,
  isDataLoading: state.isDataLoading,
  sortOrder: state.sortingOrder,
  itemsCountPerPage: state.influencers.itemsCountPerPage,
  influencersCount: state.influencers.influencersCount
});

const mapDispatchToProps = dispatch => {
  return {
    fetchInfluencerData: url => {
      dispatch(fetchInfluencerData(url));
    }
  };
};

CardContainer.propType = {
  items: PropTypes.array.isRequired
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CardContainer);
