import React, { Component } from 'react';
import logo from './logo.png';
import './Header.css';
import DropDownOnClick from '../utilities/dropdownonclick/DropDownOnClick';

class Header extends Component {
    render() {
        return (
            <header>
                <div className="logo">
                    <img src={logo} alt="logo" />
                </div>
                <div id="login" className="dropdown">
                    <DropDownOnClick
                        name="page-selected"
                        classname="select-box--box "
                        belongTo="Header"
                        items={[
                            { value: 'vijayfact@gmail.com', id: "loginid" },
                            { value: 'My Campaigns', id: "campaigns" },
                            { value: 'My Lists', id: "lists" },
                            { value: 'Edit Profile', id: "editprofile" },
                            { value: 'Log Out', id: "logout" },
                        ]}
                    />
                </div>
            </header>
        );
    }
}
export default Header;