function getFormattedURLParameters(parametersString, type) {
  let formattedParameter = "";
  let parametersArray = parametersString.split(",");
  parametersArray.map(parameter => {
    formattedParameter += `${type}=${encodeURIComponent(parameter)}&`;
  });
  return formattedParameter;
}

function getFormattedEngagement(engagementsString) {
  let formattedEngagement = "", maxengagement = -1, minengagement = 0;
  let sortedEngagementArray = [];
  if (engagementsString) {
    let engagementsArray = engagementsString.split(",");
    for (let i = 0; i < engagementsArray.length; i++) {
      engagementsArray[i] = engagementsArray[i].replace("%", "");
      engagementsArray[i] = engagementsArray[i].replace(">", "");
      sortedEngagementArray.push(engagementsArray[i].split("-"));
      sortedEngagementArray = [].concat.apply([], sortedEngagementArray);
      sortedEngagementArray = sortedEngagementArray.slice().sort(function (x, y) {
        return x - y;
      });
      maxengagement = sortedEngagementArray[sortedEngagementArray.length - 1];
      minengagement = sortedEngagementArray[0];
    }
  }
  formattedEngagement = `&min_engagement_rate=${minengagement}&max_engagement_rate=${maxengagement}`;
  return formattedEngagement;
}
const getFilter = () => {

  let cat = document.getElementById("category").value;
  let loc = document.getElementById("location").value;
  let eng = document.getElementById("engagement").value;
  let minfoll = document.getElementById("minval").value;
  let maxfoll = document.getElementById("maxval").value;

  maxfoll = maxfoll === "" ? -1 : maxfoll;
  minfoll = minfoll === "" ? 0 : minfoll;
  const filter = {
    categories: getFormattedURLParameters(cat, "categories"),
    locations: getFormattedURLParameters(loc, "location"),
    engagement: getFormattedEngagement(eng),
    minfollowers: parseInt(minfoll),
    maxfollowers: parseInt(maxfoll)
  };
  return filter;

}

export { getFilter };
