import React, { Component } from 'react';
import CheckBox from '../checkbox/CheckBox';
import ScoreInfo from '../scoreinfo/ScoreInfo';
import CategoryFormatter from '../../../utilities/categoryformatter/CategoryFormatter';
import './InfluencerModal.css';


class InfluencerModal extends Component {
    CloseInfluencerModal = (id) => {
        var modal = document.getElementById(id);
        modal.style.display = 'none';
    }
    getBigger(x, y) {
        x = parseInt(x);
        y = parseInt(y);
        var z = Math.max(x, y);
        return z;
    }
    getFormatedViews(x) {
        let y;
        if (x >= 0 && x < 1000) {
            return x;
        }
        if (x >= 1000 && x < 1000000) {
            y = (x / 1000).toFixed(1);
            return y + "K";
        } else {
            y = (x / 1000000).toFixed(1);
            return y + "M";
        }
    }
    getFormatedFollowers(x) {
        return new Intl.NumberFormat('en-IN', { maximumSignificantDigits: 3 }).format(x);
    }
    convertToInt(x) {
        return parseInt(x);
    }
    render() {
        const { data, id } = this.props;
        return (
            <div className="modal" id={id}>
                <div className="modal-content">
                    <div className="modal-header">
                        <span className="close" onClick={() => { this.CloseInfluencerModal(id) }}>&times;</span>
                    </div>
                    <div className="modal-body">
                        <div className="intro">
                            <div className="profile-pic">
                                <img src={data.photo} alt="" />
                            </div>
                            <div className="profile-info">
                                <div className="name-select">
                                    <div className="name-info">
                                        <a href="/" className="insta-handle">@{data.username}</a>
                                        <a href="/" className="actual-name">{data.full_name}</a>
                                    </div>
                                    <CheckBox outerdiv="select2"
                                        innerdiv="big-card-select"
                                        partOf="big" />
                                </div>
                                <div className="more-info">
                                    <ScoreInfo class="authen"
                                        score={this.getFormatedFollowers(data.followers)}
                                        heading="Followers" />
                                    <ScoreInfo class="authen"
                                        type="engagement"
                                        score={this.getBigger(data.avg_picture_engagement, data.avg_video_engagement)}
                                        heading="Engagement" />
                                </div>
                                <div className="location2">
                                    <i className="fas fa-map-marker-alt"></i>&nbsp;{data.location_city}
                                </div>
                                <div className="category2">
                                    <CategoryFormatter categories={data.categories__name} />
                                </div>
                                <div className="social-media">
                                    <a href={data.facebook_url} target="_blank" rel="noopener noreferrer" ><i className="fab fa-facebook-f" /></a>
                                    <a href={data.instagram_url} target="_blank" rel="noopener noreferrer"><i className="fab fa-instagram" /></a>
                                    <a href={data.twitter_url} target="_blank" rel="noopener noreferrer" ><i className="fab fa-twitter" /></a>
                                    <a href={data.youtube_url} target="_blank" rel="noopener noreferrer" ><i className="fab fa-youtube" /></a>
                                </div>
                            </div>
                        </div>
                        <div className="detailed-info">
                            <h3>Picture Post Statistics</h3>
                            <div className="more-info">
                                <ScoreInfo class="authen"
                                    type="engagement"
                                    score={this.convertToInt(data.avg_picture_engagement)}
                                    heading="Average-Engagement" />
                                <ScoreInfo class="authen"
                                    score={this.getFormatedViews(this.convertToInt(data.avg_picture_likes))}
                                    heading="Average Likes" />
                                <ScoreInfo class="authen"
                                    score={data.Avgpicturecost}
                                    heading="Estimated Picture Cost" />
                            </div>
                            <h3>Video Post Statistics</h3>
                            <div className="more-info">
                                <ScoreInfo class="authen"
                                    score={this.convertToInt(data.avg_video_engagement)}
                                    type="engagement"
                                    heading="Average-Engagement" />
                                <ScoreInfo class="authen"
                                    score={
                                        this.convertToInt(data.avg_video_views) === -1 ? "NA" : this.getFormatedViews(this.convertToInt(data.avg_video_views))}
                                    heading="Average Views" />
                                <ScoreInfo class="authen"
                                    score={data.Avgvideopostcost}
                                    heading="Estimated Video Cost" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}
export default InfluencerModal