
const setIsDataLoading = isDataLoading => {
  return {
    type: "SET_IS_DATA_LOADING",
    isDataLoading
  };
};

const receiveInfluencerData = influencerData => {
  return {
    type: "UPDATE_INFLUENCER_DATA",
    influencerData
  };
};

const fetchInfluencerData = url => dispatch => {
  dispatch(setIsDataLoading(true));
  return fetch(url)
    .then(response => response.json())
    .then(json => {
      dispatch(setIsDataLoading(false));
      dispatch(receiveInfluencerData(json));
    });
};
const recieveFilterMenuData = menuData => {
  return {
    type: "GET_MENU_DATA",
    menuData
  };
}
const fetchFilterMenuData = MenuType => dispatch => {
  const url = `https://secure-bastion-20455.herokuapp.com/categories/${MenuType}`;
  return fetch(url)
    .then(response => response.json())
    .then(json => {
      dispatch(recieveFilterMenuData(json));
    })
}
export { setIsDataLoading, receiveInfluencerData, fetchInfluencerData, fetchFilterMenuData, recieveFilterMenuData };
