import React, { Component } from 'react';
import './OrderBy.css';
import DropDownOnClick from '../../utilities/dropdownonclick/DropDownOnClick';

class OrderBy extends Component {
    render() {
        return (
            <DropDownOnClick
                name="order-selected"
                belongTo="order-by"
                classname="order-by-select"
                items={[
                    { value: 'OrderBy', id: "none" },
                    { value: 'Followers Low to High', id: "followers_ascending" },
                    { value: 'Followers High to Low', id: "followers_descending" },
                    { value: 'Engagement High to Low', id: "engagement_descending" },
                    { value: 'Engagement Low to High', id: "engagement_ascending" }
                ]}
            />
        );
    }
}
export default OrderBy;