import React, { Component } from 'react';
import ScoreInfo from '../scoreinfo/ScoreInfo';
import CheckBox from '../checkbox/CheckBox';
import InfluencerModal from '../influencermodal/InfluencerModal';
import './InfluencerCard.css';
import CategoryFormatter from '../../../utilities/categoryformatter/CategoryFormatter';

class InfluencerCard extends Component {

    checkLength(name, x) {
        if (name.length >= x) {
            return name.substring(0, x) + "..";
        } else {
            return name;
        }
    }
    getFollowersK(x) {
        let y
        if (x >= 0 && x < 1000) {
            return x
        } else
            if (x >= 1000 && x < 1000000) {
                y = (x / 1000).toFixed(1);
                return y + "K";
            } else
                if (x > 1000000) {
                    y = (x / 1000000).toFixed(1);
                    return y + "M";
                }

    }
    getBigger(x, y) {
        x = parseInt(x);
        y = parseInt(y);
        var z = Math.max(x, y);
        return z;
    }
    OpenInfluencerModal = (id) => {
        let modal = document.getElementById(id);
        modal.style.display = "block";
        window.onclick = function (event) {
            if (event.target === modal) {
                modal.style.display = "none";
            }
        }
    }
    render() {
        const { data, id } = this.props;
        let m_id = "Modal" + id;
        return (
            <React.Fragment>
                <InfluencerModal id={m_id} data={data} />
                <div className="column">
                    <div className="card">
                        <div className="info-cont" id={id}>
                            <div className="img-cont">
                                <img src={data.photo} alt="" onClick={this.OpenInfluencerModal.bind(this, m_id)} />
                            </div>
                            <div className="desc">
                                <p className="user-name" onClick={this.OpenInfluencerModal.bind(this, m_id)}>@{this.checkLength(data.username, 12)}</p>
                                <p className="full-name" onClick={this.OpenInfluencerModal.bind(this, m_id)}>{this.checkLength(data.full_name, 16)}</p>
                                <p className="location"><i className="fas fa-map-marker-alt"></i>&nbsp;{data.location_city === null ? "India" : data.location_city}</p>
                                {/* <p className={data.comments === " " ? "comment-hide" : "comment"}><i className="fa fa-comment"></i>&nbsp; {this.checkLength(data.comments, 18, "")}</p> */}
                            </div>
                        </div>
                        <div className="text-center">
                            <p><CategoryFormatter categories={data.categories__name} /></p>

                        </div>
                        <div className="more-info">
                            {/* <ScoreInfo class="authen"
                                score={data.authenticity_score}
                                heading="Authenticity Score" /> */}
                            <ScoreInfo class="authen"
                                score={this.getFollowersK(data.followers)}
                                heading="Followers" />
                            <ScoreInfo class="authen"
                                type="engagement"
                                score={this.getBigger(data.avg_picture_engagement, data.avg_video_engagement)}
                                heading="Engagement Rate" />
                        </div>
                        <CheckBox outerdiv=""
                            innerdiv="small-card-select"
                            partOf="small"
                            listarr={data.Lists} />
                    </div>
                </div>
            </React.Fragment>
        );
    }
}
export default InfluencerCard;